/* Always clear alerts
   ========================================================================== */
.alert {
  display: flex;
  align-items: flex-start;
}
.alert::after {
  display: block;
  clear: both;
  content: "";
}
.alert p {
  margin-bottom: 0;
}
.alert > .if:first-child {
  margin-right: 1.6rem;
}
.alert .c-button-toolbar {
  margin-top: 1.6rem;
}

.alert-title {
  font-size: 1.6rem;
}

.alert-small {
  font-size: 1.2rem;
}
.alert-small .alert-title {
  font-size: 1.4rem;
}

.alert-content {
  overflow: hidden;
  flex: 1;
}

.alert-dismissible .btn-close {
  opacity: 1;
  mix-blend-mode: multiply;
  padding: 0.8rem;
  top: 1rem;
  right: 1rem;
}
.alert-dismissible .btn-close .if {
  display: block;
  color: #999999;
}

/* Success alert
    ========================================================================== */
.alert-success {
  background: #F1F7ED;
  color: #586853;
}
.alert-success .alert-title {
  color: #11240F;
}
.alert-success > .if:first-child {
  color: #71A860;
}

/* Danger alert
    ========================================================================== */
.alert-danger {
  background: #FDECE7;
  color: #784d42;
}
.alert-danger .alert-title {
  color: #300D03;
}
.alert-danger > .if:first-child {
  color: #DC4E28;
}

/* Info alert
    ========================================================================== */
.alert-info {
  background: #E9F0FA;
  color: #3f5472;
  position: relative;
}
.alert-info .alert-title {
  color: #09172A;
}
.alert-info .btn-outline-secondary {
  border-color: #A8C7F0;
}
.alert-info .btn-tertiary {
  color: #3f5472;
}
.alert-info > .if:first-child {
  color: #1E64C8;
}

/* Warning alert
    ========================================================================== */
.alert-warning {
  background: #FEF7E7;
  color: #7e6743;
}
.alert-warning .alert-title {
  color: #311C02;
}
.alert-warning .text-muted {
  color: #8c7d65 !important;
}
.alert-warning .btn-tertiary {
  color: #7e6743;
}
.alert-warning > .if:first-child {
  color: #F1A42B;
}

/* Default alert
    ========================================================================== */
.alert-default {
  background: rgba(242, 242, 242, 0.8);
  color: #646464;
}
.alert-default .alert-title {
  color: #202020;
}
.alert-default > .if:first-child {
  color: #999999;
}

