  align-items: center;
  justify-content: center;
}

.if--small {
  font-size: 1.6rem;
  line-height: 1.6rem;
}

.if--large {
  font-size: 3.2rem;
  line-height: 3.2rem;
}

.if--success {
  color: #71A860;
}

.if--error {
  color: #DC4E28;
}

.if--primary {
  color: #1E64C8;
}

.if--warning {
  color: #F1A42B;
}

.if--danger {
  color: #DC4E28;
}

.if--muted {
  color: #999999;
}

#flash-messages {
  position: absolute;
  top: 2rem;
  right: 2rem;
  display: flex;
  flex-direction: column-reverse;
  background-color: transparent;
}
#flash-messages .bc-toast {
  margin: 0 1.3rem 1.3rem 0;
}